.jumbotron {
margin-top: 15px;
margin-bottom: 15px;
color: black; 
clear: both;
width: 100%; 
height: 300px;
text-align: center;
background-color: white;
background-image: url(http://www.pngall.com/wp-content/uploads/1/Community-Free-Download-PNG.png) !important;
background-size: 500px; 
font-family: 'Work Sans', sans-serif;
font-weight: normal;
}

h1 {
  font-family: 'Work Sans', sans-serif;
  font-weight: 200;
}
.delete-btn {
  float: right;
  color: #9e2a26;
  font-family: 'Work Sans', sans-serif;
}

.card {
  background-size: cover;
  background-position: center;
  background-clip: content-box;
  border: 6px solid white;
  height: 400px;
  width: 400px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  position: relative;
  margin: 0 auto;
  margin-top: 30px;
  padding-bottom: 5px;
}

.card .fa-spinner {
  font-size: 180px;
  position: absolute;
  left: 25%;
  top: 25%;
}


.navbar {
    color:black !important;
    background-color: white !important;
    border-bottom: solid 1px darkgrey;
    font-family: 'Work Sans', sans-serif;
    font-weight: normal;
}
.navbar-brand {
  font-weight: normal;
}

.navbar-light .navbar-brand {
    color: black;
}

.navbar-light .navbar-nav .nav-link {
    color: black;
}

a { color: black;

}
a:hover {
  color: rgb(75, 75, 172);
}
.footer {
    min-height: 100px;
    background: white;
    clear: both;
    width: 100%;
    font-family: 'Work Sans', sans-serif;
    border-top: solid 1px darkgrey;
    text-align: center;
    
}


